}

.about-btn, .contacts__btn-loc {
  letter-spacing: 0;
  text-align: center;
  color: #131313;
  cursor: pointer;
  background-color: #f8d24e;
  border: none;
  border-radius: 10px;
  padding: 12px 34px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.about-btn:hover, .contacts__btn-loc:hover {
  background-color: #0a8817;
}

.about-btn:focus, .contacts__btn-loc:focus {
